import React from "react";
import { motion, useAnimationControls } from "framer-motion";
import { useEffect } from "react";
import { useState } from "react";
import TUS from "../../../images/logo/TUS.jpeg";
import CommunityLab from "../../../images/logo/CommunityLab.jpg";
import IntroduceData from "../../../../../../data/tus-volunteer/Introduce.json";

const CarouselBanner = () => {
  let [image1, setImage1] = useState("");
  let [image2, setImage2] = useState("");
  let [image3, setImage3] = useState("");
  let [image4, setImage4] = useState("");

  const control1 = useAnimationControls();
  const control2 = useAnimationControls();
  const control3 = useAnimationControls();
  const control4 = useAnimationControls();

  useEffect(() => {
    let imageList = IntroduceData.map((data) => data.url);

    setImage1(imageList[imageList.length - 1]);
    setImage2(imageList[0]);
    setImage3(imageList[1]);
    setImage4(imageList[2]);

    let bannerIndex = 0;

    setInterval(() => {
      control1.start({ height: 0, transition: { duration: 0.5 } }).then(() => {
        control1.set({ height: 450 });
        control2.set({ scale: 1 });
        control3.set({ scale: 0.95 });
        control4.set({ scale: 0.9 });

        if (bannerIndex > imageList.length) bannerIndex = 0;
        else bannerIndex++;

        let trunk = imageList.shift();
        imageList.push(trunk!);

        setImage1(
          bannerIndex === 0
            ? imageList[imageList.length - 1]
            : imageList[imageList.length - 1]
        );
        setImage2(imageList[0]);
        setImage3(imageList[1]);
        setImage4(imageList[2]);
      });
      control2.start({ scale: 0.95, transition: { duration: 0.5 } });
      control3.start({ scale: 1, transition: { duration: 0.5 } });
      control4.start({ scale: 0.95, transition: { duration: 0.5 } });
    }, 5000);
  }, [control1, control2, control3, control4]);

  return (
    // h-[520px] w-[850px]
    <div className="flex flex-col h-[520px] w-[850px] overflow-hidden">
      <div className="flex flex-col relative -top-[415px] left-5">
        <motion.img
          style={{ scale: 0.95 }}
          animate={control1}
          className="w-[800px] h-[450px] object-cover rounded-3xl shadow-round overflow-hidden"
          src={image1}
        />
        <motion.img
          animate={control2}
          className="w-[800px] h-[450px] object-cover rounded-3xl shadow-round overflow-hidden"
          src={image2}
        />
        <motion.img
          style={{ scale: 0.95 }}
          animate={control3}
          className="w-[800px] h-[450px] object-cover rounded-3xl shadow-round overflow-hidden"
          src={image3}
        />
        <motion.img
          style={{ scale: 0.9 }}
          animate={control4}
          className="w-[800px] h-[450px] object-cover rounded-3xl shadow-round overflow-hidden"
          src={image4}
        />
      </div>
    </div>
  );
};

function Introduce({ className = "" }) {
  return (
    <div className={className + " flex"}>
      <div className="flex">
        <CarouselBanner />
      </div>
      <div className="flex flex-col mt-[40px] gap-10">
        <div className="flex gap-5 justify-center">
          <img src={CommunityLab} className="h-16" />
          <img src={TUS} className="h-16" />
        </div>
        <div>
          <div className="text-[40px] ml-[50px] mb-[35px]">
            <b> 「つながる場」 </b>をつくる
          </div>
          <div className="text-[20px] ml-[90px]">
            Community Labは、<b className="text-[#3CB3EC]">現役大学生</b>
            が子供たちに
            <br />
            様々な分野のイベントやコンテンツを提供する
            <b className="text-[#3CB3EC]">教育コミュニティ</b>
            です。
            <br />
            <br />
            ​年齢や学校の枠を越え
            <br />
            生涯に渡って交流する仲間と
            <b className="text-[#3CB3EC]"> 「つながる場」 </b>
            をつくります。
            <br />
            <br />
            {/* <b className="text-[15px] text-[#3CB3EC]">詳細はこちらへ！</b> */}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Introduce;
